/**
 * 
 */
const axiosAPI = function (obj) {
  return axios({
    configURL: config.addressBookURL,
    url: obj.url ? obj.url : '/zero-box/mailList',
    method: obj.method,
    params: obj.params,
    data: obj.data
  });
};

/**
 * 
 * @params obj {Object} 
 *  obj.name {String}  
 */
export const addCompanya = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.mailList.add_companya'
    },
    data: {
      name: obj.name
    }
  });
};

/**
 * 
 * @params obj {Object} 
 *  obj.name {String}  *
 *  obj.company_id {String}  id *
 *  obj.parentId {String}  id 
 */
export const addDepartment = (obj) => {
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.mailList.add_department'
    },
    data: {
      name: obj.name,
      company_id: obj.company_id,
      parentId: obj.parentId
    }
  });
};

