import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store/index'
import './assets/css/common.css'

//  element-ui   ---->>>>>> 
/*   */
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

import CHeader from './components/c_header.vue'
import CSideBar from './components/c_sidebar.vue'
import CContentHeader from './components/c_contentHeader.vue'


import api from './API/index.js' // 
import ZERO from './API/zero' // 
Vue.prototype.$api = api;
Vue.prototype.$zero = ZERO;


//    
Vue.component('CHeader', CHeader); // UI
Vue.component('CSideBar', CSideBar); // UI
Vue.component('CContentHeader', CContentHeader); // UI


Vue.use(ElementUI);
Vue.config.productionTip = false

//   session  


// 
router.beforeEach((to, from , next) => {
  if(to.meta.title){
    document.title = to.meta.title;
  }
  next();
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
