import axios from 'axios/index'
import config from './config'
import zero from './zero'

const AXIOS = {
  /**
   * 
   * @params obj {Object} 
   *  obj.method {String}   GET POST   GET
   *  obj.configURL {String} ip http://127.0.0.1:3000  config url
   *  obj.url {String}   /aaa
   *  obj.params {JSON} get  {aa: 1, bb: 2}
   *  obj.data {JSON} post
   *  obj.header    application/json;charset=utf-8
   */

  server: (obj) => {
    return new Promise((resolve, reject) => {
      obj.method = obj.method || 'GET';
      obj.configURL = obj.configURL || config.url;
      obj.url = obj.url || config.url;
      obj.params = obj.params || {};
      obj.data =  obj.data || {};
      obj.headers = obj.headers || {'Content-Type': 'application/json;charset=utf-8'};
      axios({
        url: obj.configURL + obj.url,
        method: obj.method,
        header: obj.header,
        params: obj.params,
        data: obj.data
      })
        .then(res => {
          if(res.status === 200){
            resolve(res.data);
          }else{
            resolve({
              /**
               *  200
               */
              status: 75400,
            })
          }
          //  token
        })
        .catch(err => {
          // zero.ToastError('');
          reject({
            status: 500,
            data: err
          });
        })
    })
  },

  /**
   * token
   */
//   getToken() {
//     return new Promise((resolve, reject) => {

//     })
//   }
};

export default AXIOS.server;
