import axios from '../axios'
import config from '../config'

/**
 * 
 * @author Zyc
 */

/*
  
    App
    
    base64
*/

/**
 * 
 */
const axiosAPI = function (obj) {
  return axios({
    configURL: config.filesURL,
    url: obj.url ? obj.url : '/zero-box/file',
    method: obj.method,
    params: obj.params,
    data: obj.data
  });
};

/**
 * App
 * @params obj {Object}
 *  obj.company_id {String} id *
 *  obj.user_id {String} id *
 *  app_file {file }  FormData *
 */
export const uploadAppPackage = (obj) => {
  return axiosAPI({
    method: 'POST',
    header: {
      'Content-Type': 'multipart/form-data'
    },
    params: {
      api: 'zero.box.files.app.uploader',
      company_id: obj.company_id,
      user_id: obj.user_id
    },
    data: obj.app_file
  });
};

/**
 * 
 * @params obj {Object}
 *  obj.company_id {String} id *
 *  obj.user_id {String} id *
 *  file {file}   *
 */
export const uploadImg = (obj) => {
  return axiosAPI({
    method: 'POST',
    header: {
      'Content-Type': 'multipart/form-data'
    },
    params: {
      api: 'zero.box.files.img.uploader',
      company_id: obj.company_id,
      user_id: obj.user_id,
    },
    data: obj.file
  });
};


/**
 * base64
 * @params obj {Object}
 *  obj.company_id {String} id *
 *  obj.user_id {String} id *
 *  obj.base64s {Array} *
 */
export const base64toUrl = (obj) => {
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.files.img.base64s2img',
      company_id: obj.company_id,
      user_id: obj.user_id,
    },
    data: {
      base64s: obj.base64s
    }
  });
};