/**
 *  API
 */
import axios from '../axios'
import config from '../config'


/**
 * 
 * 
 * 
 * 
 * 
 * 
 * Andriod
 * IOS
 * H5
 * 
 * 
 * 
 * 
 * 
 * 
 * 
 * SDK
 */

/**
 * 
 */
const axiosAPI = function (obj) {
  return axios({
    configURL: config.applicationURL,
    url: obj.url ? obj.url : '/zero-box/application',
    method: obj.method,
    params: obj.params,
    data: obj.data
  });
};

/**
 * 
 * @params obj {Object} 
 *  obj.company_id {String} id 
 */
export const getPlatformList = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.application.platform.get',
      company_id: obj.company_id
    }
  });
};

/**
 * 
 * @params {Object} obj
 *  obj.company_id {String} id
 */
export const getTypeList = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.application.type.get',
      company_id: obj.company_id
    }
  });
};

/**
 * 
 * @params {Object} obj
 *  obj.company_id {String} id
 *  obj._name {String} 
 *  obj.level {Number}  0
 */
export const createNewType = (obj) => {
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.application.type.create',
      company_id: obj.company_id
    },
    data: {
      _name: obj._name,
      level: obj.level
    }
  });
};

/**
 * 
 * @params {Object} obj
 *  obj.company_id {String} id
 *  obj.platform_alias {String}    SMALL H5 IOS ANDROID
 *  obj.pageIndex {Number} 
 *  obj.pageSize {Number}  15
 *  obj.status {Satus}  0 1 2
 */
export const getAppList = (obj) => {
  return axiosAPI({
    params: {
      api: 'zero.box.application.app.get',
      company_id: obj.company_id,
      platform_alias: obj.platform_alias,
      pageIndex: obj.pageIndex,
      pageSize: obj.pageSize || 15,
      status: obj.status,
      user_id: obj.user_id
    }
  });
};

/**
 * Andriod
 * @params {Object} obj
 *  obj.company_id {String} id *
 *  obj._name {String}  *
 *  obj.typeid {String} id id id *
 *  obj.icon {String} url logourl *
 *  obj.description {String}  *
 *  obj.founder {String} id   userid *
 *  obj.manages {Array String}    eg: ['user01', 'user01']; *
 *  obj.allow_ranges {Array}  *
 *  obj.agentid {String} agentid *
 *  obj.build {String}   build   eg: "build 11" *
 *  obj.storage_url {String}   http *
 *  obj.version {String} app *
 *  obj.size {Number}    eg: 14.2M *
 *  obj.update_description {String}  *
 *  obj.package_type {String}   eg *
 *  obj.previews {Array} 
 */
export const createAndriod = (obj) => {
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.application.app.createAndroid',
      company_id: obj.company_id
    },
    data: {
      _name: obj._name,
      typeid: obj.typeid,
      icon: obj.icon,
      description: obj.description,
      founder: obj.founder,
      allow_ranges: obj.allow_ranges,
      manages: obj.manages,
      agentid: obj.agentid,
      previews: obj.previews,
      build: obj.build,
      storage_url: obj.storage_url,
      version: obj.version,
      size: obj.size,
      update_description: obj.update_description,
      package_type: obj.package_type
    }
  });
};

/**
 * IOS
 * @params {Object} obj
 *  obj.company_id {String} id *
 *  obj._name {String}  *
 *  obj.typeid {String} id id id *
 *  obj.icon {String} url logourl *
 *  obj.description {String}  *
 *  obj.founder {String} id   userid *
 *  obj.manages {Array String}    eg: ['user01', 'user01']; *
 *  obj.allow_ranges {Array}  *
 *  obj.agentid {String} agentid *
 *  obj.build {String}   build   eg: "build 11" *
 *  obj.storage_url {String}   http *
 *  obj.version {String} app *
 *  obj.size {Number}    eg: 14.2M *
 *  obj.update_description {String}  *
 *  obj.package_type {String}   eg *
 */
export const createIOS = (obj) => {
  return axiosAPI({
    method: 'POST',
    params: {
      api: 'zero.box.application.app.createIos',
      company_id: obj.company_id
    },
    data: {
      _name: obj._name,
      typeid: obj.typeid,
      icon: obj.icon,
      description: obj.description,
      founder: obj.founder,
      allow_ranges: obj.allow_ranges,
      manages: obj.manages,
      agentid: obj.agentid,
      build: obj.build,
      storage_url: obj.storage_url,
      source_name: obj.source_name,
      // obj. storage_url source_name
      version: obj.version,
      size: obj.size,
      update_description: obj.update_description,
      package_type: obj.package_type,
      previews: obj.previews,
      factory: "5d15b2924e53838850469509"
    }
  });
};

